'use client'

import { motion } from 'motion/react'
import { useState, useEffect, useRef } from 'react'
import { 
  Video, 
  VideoOff, 
  Mic, 
  MicOff, 
  Phone, 
  MessageSquare, 
  SkipForward,
  Flag,
  Volume2,
  VolumeX
} from 'lucide-react'

function init_handlers(ws:WebSocket){
  
}

export default function VideoChat() {
  const senderRef = useRef<HTMLVideoElement|null>(null);
  const recieverRef = useRef<HTMLVideoElement|null>(null);
  const [isVideoEnabled, setIsVideoEnabled] = useState(true)
  const [isMicEnabled, setIsMicEnabled] = useState(true)
  const [isSpeakerEnabled, setIsSpeakerEnabled] = useState(true)
  const [isConnected, setIsConnected] = useState(true);
  const [connectionTime, setConnectionTime] = useState(0);
  const [ws,setWs] = useState<WebSocket|null>(null);
  const [pc, setPc] = useState<RTCPeerConnection|null>(null);
  const [localAudioTrack, setlat] = useState<MediaStreamTrack|null>(null);
  const [localVideoTrack, setlvt] = useState<MediaStreamTrack|null>(null);

  

  useEffect(()=>{
    // const configuration = {'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}]}
   
  },[]);


  async function init_connection(){
    const configuration = {'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}]}
    const pc = new RTCPeerConnection(configuration);
    await getTrack(pc);
    
  }
  async function getTrack(pc:RTCPeerConnection){
    const stream = await window.navigator.mediaDevices.getUserMedia({video:true,audio:true});
    const audioStream = stream.getAudioTracks()[0];
    const videoStream = stream.getVideoTracks()[0];
    pc.addTrack(audioStream);
    pc.addTrack(videoStream);
    setlat(audioStream);
    setlvt(videoStream);
  }


  // Mock connection timer
  useEffect(() => {
    if (isConnected) {
      const timer = setInterval(() => {
        setConnectionTime(prev => prev + 1)
      }, 1000)
      return () => clearInterval(timer)
    }
  }, [isConnected])

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }

  const handleEndCall = () => {
    setIsConnected(false)
    setConnectionTime(0)
  }

  const handleSkipUser = () => {
    // Mock skip functionality
    setConnectionTime(0)
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-purple-900 to-violet-900 relative overflow-hidden">
      {/* Background Elements */}
      <div className="absolute inset-0 overflow-hidden">
        <motion.div
          className="absolute top-1/4 left-1/4 w-32 h-32 bg-gradient-to-r from-pink-400/20 to-purple-500/20 rounded-full blur-xl"
          animate={{
            y: [0, -20, 0],
            x: [0, 20, 0]
          }}
          transition={{
            duration: 8,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />
        <motion.div
          className="absolute bottom-1/4 right-1/4 w-24 h-24 bg-gradient-to-r from-cyan-400/20 to-blue-500/20 rounded-full blur-xl"
          animate={{
            y: [0, 20, 0],
            x: [0, -20, 0]
          }}
          transition={{
            duration: 6,
            repeat: Infinity,
            ease: "easeInOut",
            delay: 2
          }}
        />
      </div>

      {/* Header */}
      <motion.header 
        className="relative z-10 p-4 flex justify-between items-center backdrop-blur-sm bg-black/20"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <div className="flex items-center gap-3">
          <motion.div
            className="p-2 bg-gradient-to-r from-pink-500 to-violet-500 rounded-full"
            animate={{
              boxShadow: [
                "0 0 20px rgba(236, 72, 153, 0.3)",
                "0 0 40px rgba(236, 72, 153, 0.6)",
                "0 0 20px rgba(236, 72, 153, 0.3)"
              ]
            }}
            transition={{
              duration: 3,
              repeat: Infinity,
              ease: "easeInOut"
            }}
          >
            <Video className="w-6 h-6 text-white" />
          </motion.div>
          <h1 className="text-2xl font-bold bg-gradient-to-r from-pink-400 to-cyan-400 bg-clip-text text-transparent">
            OMICRON
          </h1>
        </div>
        
        <div className="flex items-center gap-4 text-white">
          <motion.div 
            className="flex items-center gap-2 px-3 py-1 bg-white/10 backdrop-blur-sm rounded-full border border-white/20"
            animate={{
              scale: isConnected ? [1, 1.02, 1] : 1
            }}
            transition={{
              duration: 2,
              repeat: isConnected ? Infinity : 0
            }}
          >
            <div className={`w-2 h-2 rounded-full ${isConnected ? 'bg-green-400' : 'bg-red-400'}`} />
            <span className="text-sm font-medium">
              {isConnected ? formatTime(connectionTime) : 'Disconnected'}
            </span>
          </motion.div>
        </div>
      </motion.header>

      {/* Main Video Area */}
      <motion.div 
        className="relative z-10 p-4 h-[calc(100vh-80px-80px)]"
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.5, delay: 0.2 }}
      >
        {/* Desktop: Side by side layout */}
        <div className="hidden lg:grid lg:grid-cols-2 gap-4 h-full">
          {/* Your Video */}
          <motion.div 
            className="relative bg-gradient-to-br from-gray-800 to-gray-900 rounded-2xl overflow-hidden shadow-2xl border border-white/10"
            whileHover={{ scale: 1.02 }}
            transition={{ type: "spring", stiffness: 300 }}
          >
            <div className="absolute top-4 left-4 z-10">
              <motion.div 
                className="px-3 py-1 bg-black/50 backdrop-blur-sm rounded-full text-white text-sm font-medium"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.5 }}
              >
                You
              </motion.div>
            </div>
            
            {/* Mock Video Feed */}
            <div className="w-full h-full flex items-center justify-center">
              {isVideoEnabled ? (
                <div className="w-full h-full bg-gradient-to-br from-blue-900 via-purple-900 to-pink-900 relative overflow-hidden">
                  <motion.div
                    className="absolute inset-0 bg-gradient-to-br from-transparent via-white/5 to-transparent"
                    animate={{
                      rotate: [0, 360]
                    }}
                    transition={{
                      duration: 20,
                      repeat: Infinity,
                      ease: "linear"
                    }}
                  />
                  <div className="absolute inset-0 flex items-center justify-center">
                    <motion.div
                      className="w-32 h-32 bg-gradient-to-r from-cyan-400 to-pink-400 rounded-full opacity-30"
                      animate={{
                        scale: [1, 1.2, 1],
                        opacity: [0.3, 0.6, 0.3]
                      }}
                      transition={{
                        duration: 3,
                        repeat: Infinity,
                        ease: "easeInOut"
                      }}
                    />
                  </div>
                </div>
              ) : (
                <motion.div 
                  className="flex flex-col items-center text-gray-400"
                  initial={{ scale: 0 }}
                  animate={{ scale: 1 }}
                  transition={{ type: "spring", stiffness: 200 }}
                >
                  <VideoOff className="w-16 h-16 mb-4" />
                  <p className="text-lg font-medium">Video Disabled</p>
                </motion.div>
              )}
            </div>
          </motion.div>

          {/* Stranger's Video */}
          <motion.div 
            className="relative bg-gradient-to-br from-gray-800 to-gray-900 rounded-2xl overflow-hidden shadow-2xl border border-white/10"
            whileHover={{ scale: 1.02 }}
            transition={{ type: "spring", stiffness: 300 }}
          >
            <div className="absolute top-4 left-4 z-10">
              <motion.div 
                className="px-3 py-1 bg-black/50 backdrop-blur-sm rounded-full text-white text-sm font-medium"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.7 }}
              >
                Stranger
              </motion.div>
            </div>
            
            {/* Mock Stranger Video Feed */}
            <div className="w-full h-full flex items-center justify-center">
              {isConnected ? (
                <div className="w-full h-full bg-gradient-to-br from-green-900 via-teal-900 to-blue-900 relative overflow-hidden">
                  <motion.div
                    className="absolute top-1/4 left-1/4 w-32 h-32 bg-gradient-to-r from-green-400/30 to-blue-400/30 rounded-full blur-xl"
                    animate={{
                      y: [0, -40, 0],
                      x: [0, 40, 0]
                    }}
                    transition={{
                      duration: 6,
                      repeat: Infinity,
                      ease: "easeInOut"
                    }}
                  />
                  <motion.div
                    className="absolute bottom-1/4 right-1/4 w-24 h-24 bg-gradient-to-r from-teal-400/30 to-cyan-400/30 rounded-full blur-xl"
                    animate={{
                      y: [0, 40, 0],
                      x: [0, -40, 0]
                    }}
                    transition={{
                      duration: 8,
                      repeat: Infinity,
                      ease: "easeInOut",
                      delay: 1
                    }}
                  />
                </div>
              ) : (
                <motion.div 
                  className="flex flex-col items-center text-gray-400"
                  initial={{ scale: 0 }}
                  animate={{ scale: 1 }}
                  transition={{ type: "spring", stiffness: 200 }}
                >
                  <div className="w-16 h-16 mb-4 rounded-full bg-gray-600 flex items-center justify-center">
                    <span className="text-2xl">?</span>
                  </div>
                  <p className="text-lg font-medium">Waiting for connection...</p>
                </motion.div>
              )}
            </div>
          </motion.div>
        </div>

        {/* Mobile/Tablet: Vertical layout */}
        <div className="lg:hidden flex flex-col gap-4 h-full">
          {/* Stranger's Video - Top */}
          <motion.div 
            className="relative bg-gradient-to-br from-gray-800 to-gray-900 rounded-2xl overflow-hidden shadow-2xl border border-white/10 flex-1"
            whileHover={{ scale: 1.02 }}
            transition={{ type: "spring", stiffness: 300 }}
          >
            <div className="absolute top-4 left-4 z-10">
              <motion.div 
                className="px-3 py-1 bg-black/50 backdrop-blur-sm rounded-full text-white text-sm font-medium"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.5 }}
              >
                
              </motion.div>
            </div>
            
            <div className="w-full h-full flex items-center justify-center">
              {isConnected ? (
                <div className="w-full h-full bg-gradient-to-br from-green-900 via-teal-900 to-blue-900 relative overflow-hidden">
                  <motion.div
                    className="absolute inset-0 bg-gradient-to-br from-transparent via-white/5 to-transparent"
                    animate={{ rotate: [0, 360] }}
                    transition={{ duration: 15, repeat: Infinity, ease: "linear" }}
                  />
                </div>
              ) : (
                <div className="flex flex-col items-center text-gray-400">
                  <div className="w-12 h-12 mb-2 rounded-full bg-gray-600 flex items-center justify-center">
                    <span className="text-lg">?</span>
                  </div>
                  <p className="text-sm font-medium">Waiting...</p>
                </div>
              )}
            </div>
          </motion.div>

          {/* Your Video - Bottom */}
          <motion.div 
            className="relative bg-gradient-to-br from-gray-800 to-gray-900 rounded-2xl overflow-hidden shadow-2xl border border-white/10 flex-1"
            whileHover={{ scale: 1.02 }}
            transition={{ type: "spring", stiffness: 300 }}
          >
            <div className="absolute top-4 left-4 z-10">
              <motion.div 
                className="px-3 py-1 bg-black/50 backdrop-blur-sm rounded-full text-white text-sm font-medium"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.7 }}
              >
                You 
              </motion.div>
            </div>
            
            <div className="w-full h-full flex items-center justify-center">
              {isVideoEnabled ? (
                <div className="w-full h-full bg-gradient-to-br from-blue-900 via-purple-900 to-pink-900 relative overflow-hidden">
                  <motion.div
                    className="absolute inset-0 bg-gradient-to-br from-transparent via-white/5 to-transparent"
                    animate={{ rotate: [0, -360] }}
                    transition={{ duration: 25, repeat: Infinity, ease: "linear" }}
                  />
                </div>
              ) : (
                <div className="flex flex-col items-center text-gray-400">
                  <VideoOff className="w-12 h-12 mb-2" />
                  <p className="text-sm font-medium">Video Off</p>
                </div>
              )}
            </div>
          </motion.div>
        </div>
      </motion.div>

      {/* Controls Footer */}
      <motion.footer 
        className="relative z-10 p-4 backdrop-blur-sm bg-black/20 border-t border-white/10"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.4 }}
      >
        <div className="flex justify-center items-center gap-4">
          {/* Video Toggle */}
          <motion.button
            className={`p-4 rounded-full transition-all ${
              isVideoEnabled 
                ? 'bg-white/10 hover:bg-white/20 text-white' 
                : 'bg-red-500 hover:bg-red-600 text-white'
            }`}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => setIsVideoEnabled(!isVideoEnabled)}
          >
            {isVideoEnabled ? <Video className="w-6 h-6" /> : <VideoOff className="w-6 h-6" />}
          </motion.button>

          {/* Mic Toggle */}
          <motion.button
            className={`p-4 rounded-full transition-all ${
              isMicEnabled 
                ? 'bg-white/10 hover:bg-white/20 text-white' 
                : 'bg-red-500 hover:bg-red-600 text-white'
            }`}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => setIsMicEnabled(!isMicEnabled)}
          >
            {isMicEnabled ? <Mic className="w-6 h-6" /> : <MicOff className="w-6 h-6" />}
          </motion.button>

          {/* Speaker Toggle */}
          <motion.button
            className={`p-4 rounded-full transition-all ${
              isSpeakerEnabled 
                ? 'bg-white/10 hover:bg-white/20 text-white' 
                : 'bg-red-500 hover:bg-red-600 text-white'
            }`}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => setIsSpeakerEnabled(!isSpeakerEnabled)}
          >
            {isSpeakerEnabled ? <Volume2 className="w-6 h-6" /> : <VolumeX className="w-6 h-6" />}
          </motion.button>

          {/* Skip User */}
          <motion.button
            className="p-4 rounded-full bg-blue-500 hover:bg-blue-600 text-white transition-all"
            whileHover={{ scale: 1.1, rotate: 10 }}
            whileTap={{ scale: 0.95 }}
            onClick={handleSkipUser}
          >
            <SkipForward className="w-6 h-6" />
          </motion.button>

          {/* End Call */}
          <motion.button
            className="p-4 rounded-full bg-red-500 hover:bg-red-600 text-white transition-all"
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
            onClick={handleEndCall}
          >
            <Phone className="w-6 h-6 rotate-[135deg]" />
          </motion.button>

          {/* Chat Toggle */}
          <motion.button
            className="p-4 rounded-full bg-white/10 hover:bg-white/20 text-white transition-all"
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
          >
            <MessageSquare className="w-6 h-6" />
          </motion.button>

          {/* Report */}
          <motion.button
            className="p-4 rounded-full bg-white/10 hover:bg-white/20 text-white transition-all"
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
          >
            <Flag className="w-6 h-6" />
          </motion.button>
        </div>
      </motion.footer>
    </div>
  )
}